}

body{margin:0;padding:0;box-sizing:border-box;color:white;font-family:Raleway}body .intro{color:#000036;font-size:10vh;margin:0;position:fixed;top:50vh;left:50vw;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center;-webkit-animation:fadein 1s alternate infinite;animation:fadein 1s alternate infinite}body .intro span{font-size:5vh;padding:0;-webkit-animation:fadein 2s alternate infinite;animation:fadein 2s alternate infinite}body .app{height:100vh;background-position:0 0;background-size:cover;animation:fadein 2s, scrolling 30s infinite alternate-reverse}body .app .overlay{background-color:rgba(0,0,54,0.6);animation:vaporwave 10s infinite alternate-reverse;width:100vw;height:100vh}body .app form{background-color:transparent;position:fixed;top:20vh;left:50vw;width:75vw;height:10vh;-webkit-transform:translate(-50%, 0);transform:translate(-50%, 0);border-radius:10%;border:0.5vh solid white}body .app form input{height:100%;width:70%;font-size:5vh;border-radius:20%;border:none;background-color:transparent;color:white;font-family:Raleway}body .app form button{width:28%;height:100%;font-size:5vh;background-color:transparent;border:none;color:white}body .app .results{position:absolute;-webkit-animation:fadein 2s;animation:fadein 2s;width:100vw;top:40vh;text-align:center;font-size:7vh}body .app .results .temp{font-size:10vh}body .app .results img{height:5vh}body .app .info{position:fixed;top:10vh;left:50vw;font-size:4vh;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);-webkit-animation:fadein 1s;animation:fadein 1s}@-webkit-keyframes vaporwave{0%{background-color:rgba(0,0,0,0.8)}25%{background-color:rgba(0,0,100,0.8)}50%{background-color:rgba(50,0,54,0.8)}75%{background-color:rgba(50,0,100,0.8)}100%{background-color:rgba(0,0,54,0.8)}}@keyframes vaporwave{0%{background-color:rgba(0,0,0,0.8)}25%{background-color:rgba(0,0,100,0.8)}50%{background-color:rgba(50,0,54,0.8)}75%{background-color:rgba(50,0,100,0.8)}100%{background-color:rgba(0,0,54,0.8)}}@-webkit-keyframes fadein{0%{opacity:0}100%{opacity:1}}@keyframes fadein{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes scrolling{0%{background-position:0 0}100%{background-position:100% 0}}@keyframes scrolling{0%{background-position:0 0}100%{background-position:100% 0}}@-webkit-keyframes ridein{0%{font-size:0}100%{font-size:8vh}}@keyframes ridein{0%{font-size:0}100%{font-size:8vh}}
